import React from 'react'
import "./navbar.css"
import Logo from "../../assets/logo-black.svg"


const NavBar = () => {
    return (
        <nav>
            <div>
                <img className="logo-nav" src={Logo} alt="logo" />
            </div>
            <div className="link-wrapper">
                <div>scarpe</div>
                <div>candele</div>
                <div>centro tavola</div>
                <div>fotobook</div>
                <div>altro</div>
            </div>
        </nav>
    )
}

export default NavBar